import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    // initial state
    tasks: [
      {
        id: 1,
        name: 'ゲームを買う',
        done: false
      }
    ]
  }
})

export default store


